<template>
  <div class="container">
    <div class="toptip">
      <span>忘记密码</span>
    </div>
    <div class="centerbox">
        <van-field
          v-if="chooseTel"
          v-model.trim="forModel.tel"
          :error="usertel"
          type="number"
          maxlength="11"
          center
          clearable
          placeholder="请输入手机号"
          label="手机号"
        />
        <van-field
          v-if="login"
          v-model.trim="forModel.tel"
          readonly
          type="number"
          center
          clearable
          label="手机号"
        />
        <van-field
          v-if="login"
          v-model.trim="forModel.password"
          type="password"
          maxlength="6"
          center
          clearable
          placeholder="请输入新密码"
          label="新密码"
        />
        <van-field
          v-if="login"
          v-model.trim="forModel.rpassword"
          type="password"
          maxlength="6"
          center
          clearable
          placeholder="请再次输入新密码"
          label="新密码"
        />
        <van-field
          v-if="show"
          v-model.trim="question"
          readonly
          center
          label="密保问题"
        />
        <van-field
          v-if="show"
          v-model.trim="answer"
          maxlength="6"
          center
          clearable
          label="密保答案"
          placeholder="请输入答案"
        />
      <div class="btnStyle">
        <van-button round block size="small" color="#00887a" v-if="chooseTel" :loading="loading" loading-text="等待中..." @click="first">下一步</van-button>
        <van-button round block size="small" color="#00887a" v-if="show"  :loading="loading" loading-text="等待中..." @click="second">下一步</van-button>
        <van-button round block size="small" color="#00887a" v-if="login" :loading="loading" loading-text="等待中..." @click="last">确定</van-button>
      </div>
      <div class="pwdreg">
        <span @click="goback">返回登录界面</span>
      </div>
    </div>
  </div>
</template>

<script>
import { validateTel } from "@/utils/validator";
import { Toast } from "vant";
import { userForget, userForgetTel, userForgetAnswer} from "@/api/user";
export default {
  name: "forgetpwd",
  data() {
    return {
      usertel: false,
      loading: false,
      chooseTel: true,
      show: false, // 显示密保
      login: false, // 显示密码
      question: "",
      answer: "",
      forModel: {
        tel: "",
        password: "",
        rpassword: "",
        smscode: ""
      }
    };
  },
  watch: {
    tel(value) {
      if (value.length === 11) {
        if (validateTel(value)) {
          this.usertel = false;
        } else {
          this.usertel = true;
          Toast("手机号格式错误");
        }
      } else {
        this.usertel = false;
      }
    }
  },
  methods: {
    goback() {
      this.$router.push("/login");
    },
    async first() {
      console.log(this.forModel.tel)
      if (!this.forModel.tel) {
        Toast("手机号不能为空");
        return;
      }
      if (!validateTel(this.forModel.tel)) {
        Toast("手机号格式有误");
        return;
      }
      this.loading = true;
      let model = {
        tel:this.forModel.tel
      }
      let res = await userForgetTel(model);
      if (res) {
        if (res.type === 1) {
          //已注册
          setTimeout(() => {
            this.loading = false;
            this.show = true
            this.chooseTel = false
            this.login = false
            this.question = res.data.question
          }, 2000);
        } else if (res.type === 3) {
          //未注册
          this.loading = false;
          Toast(res.message);
        }
      } else {
        Toast("似乎出现了错误");
        this.loading = false;
      }
    },
    async second() {
      if (!this.answer) {
        Toast("密保答案不能为空");
        return;
      }
        let model = {
        tel:this.forModel.tel,
        answer: this.answer
      }
      this.loading = true;
      let res = await userForgetAnswer(model)
      if (res) {
        if (res.type === 1) {
          //密保答案正确
          setTimeout(() => {
            this.loading = false;
            this.show = false
            this.chooseTel = false
            this.login = true
          }, 2000);
        } else if (res.type === 3) {
          //密保答案错误
          this.loading = false;
          Toast(res.message);
        }
      } else {
        Toast("似乎出现了错误");
        this.loading = false;
      }
    },
    async last() {
      if (!this.forModel.password) {
        Toast("新密码不能为空");
        return;
      }
      if (this.forModel.password !== this.forModel.rpassword) {
        Toast("两次密码不一致");
        return;
      }
      this.loading = true;
      let res = await userForget(this.forModel);
      if (res) {
        if (res.type === 1) {
          //已注册
          setTimeout(() => {
            this.loading = false;
            this.$router.push("/login");
            this.chooseTel = true;
            this.show = false; // 显示密保
            this.login = false // 显示密码
          }, 2000);
        } else if (res.type === 3) {
          //注册出错
          this.loading = false;
        }
        Toast(res.message);
      } else {
        Toast("似乎出现了错误");
        this.loading = false;
      }
      this.forModel.tel = ""
      this.forModel.password = ""
      this.forModel.rpassword = ""
    }
  }
};
</script>
<style lang="scss" scoped>
.van-cell {
  width: 80%;
  margin: 0 auto;
  padding: 10px 0;
  color: #707070
}
.van-cell:not(:last-child)::after {
  position: absolute;
  box-sizing: border-box;
  content: ' ';
  pointer-events: none;
  right: 0;
  bottom: 0;
  left: 0;
  border-bottom: 1px solid #ebedf0;
  -webkit-transform: scaleY(.5);
  transform: scaleY(.5);
}
.container {
  height: calc(100vh - 46px);
  position: relative;
  .toptip {
    position: relative;
    width: 100%;
    height: 100px;
    padding: 10px 0 20px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      width: 100%;
      text-align: center;
      font-size: 32px;
    }
  };
  .centerbox {
    .btnStyle {
      width: 80% !important;
      margin: 0 auto;
      margin-top: 50px;
    };
    .pwdreg {
      position: relative;
      margin-top: 10px;
      text-align: center;
      span {
        margin: 0 5px 0 5px;
        font-size: 12px;
      }
    }
  }
  
}
</style>