import {get, post} from '@/utils/request'

// 注册账号
export async function userReg(model) {
    return await post('user/reg', model)
}
// 登录账号
export async function userLogin(model) {
    return await post('user/login', model)
}
// 用户更新个人资料
export async function userUpdate(model) {
    return await post('user/update', model)
}
// 用户忘记密码
export async function userForget(model) {
    return await post('user/forget', model)
}
// 用户修改密码
export async function userChangePwd(model) {
    return await post('user/changePwd', model)
}
// 用户修改密保
export async function userMibaoSet(model) {
    return await post('user/mibaoSet', model)
}
// 判断用户注册没有
export async function userForgetTel(model) {
    return await post('user/forgetTel', model)
}
// 判断用户输入的密保答案是否正确
export async function userForgetAnswer(model) {
    return await post('user/forgetAnswer', model)
}