/**
 * 验证整数
 * @param {Object} number
 */
export function validateNumber(number) {
	var reg = /^[0-9]*[0-9][0-9]*$/;
	return reg.test(number);
}
/**
 * 验证手机号码
 * @param {Object} tel
 */
export function validateTel(tel) {
	var reg = /^1[3|4|5|6|7|8|9][0-9]\d{8}$/;
	return reg.test(tel);
}

/**
 * 验证座机或者手机号码
 * @param {Object} phone
 */
export function validatePhoneOrTel(phone) {
	var reg = /^(((\+\d{2}-)?0\d{2,3}-\d{7,8})|((\+\\d{2}-)?(\d{2,3}-)?([1][3,4,5,6,7,8,9][0-9]\d{8})))$/;
	return reg.test(phone);
}